<script>
export default {
  name: "Navbar",
  data() {
    return {
      name: "BootstrapVue",
      img: require("../assets/icon-144x144.png"),
      checked: true,
      img_user: require("../assets/user_img.png"),
      dark: false,
    };
  },
  watch: {
    show(newVal) {
      console.log("Alert is now " + (newVal ? "visible" : "hidden"));
    },
  },
  methods: {
    modelBrn() {
      this.dark = !this.dark;
      if (this.dark) {
        window.document.documentElement.setAttribute("data-theme", "dark");
      } else {
        window.document.documentElement.setAttribute("data-theme", "light");
      }
    },
    goindex() {
      this.$router.push("/");
    },
    c() {
      console.log("1");
    },
    toggle() {
      console.log("Toggle button clicked");
      this.show = !this.show;
    },
    dismissed() {
      console.log("Alert dismissed");
    },
    go(id) {
      switch (id) {
        case 1:
          this.$router.push("/swaps");
          break;
        case 2:
          this.$router.push("/eip-1559");
          break;
        case 3:
          this.$router.push("/faqs");
          break;
        case 4:
          this.$router.push("/4");
          break;
        case 5:
          this.$router.push("/5");
          break;
        case 6:
          this.$router.push("/team");
          break;
        case 7:
          this.$router.push("/7");
          break;
        case 8:
          this.$router.push("/8");
          break;
        case 9:
          this.$router.push("/9");
          break;
        case 10:
          this.$router.push("/institutions");
          break;
        case 11:
          this.$router.push("/flask");
          break;
      }
    },
    goo() {
      this.$router.push("/download");
    },
  },
  created() {},
  mounted() {
    window.document.documentElement.setAttribute("data-theme", "light");
  },
};
</script>

